    --main-bg-color: #19181a;
    --priority-low-color: #479761;
    --priority-med-color: #cebc81;
    --priority-high-color: #a16e83;
    --secondary-color: #b19f9e;
}

* {
    margin: 0;
    padding: 0;
}

body {
    background-color: var(--main-bg-color);
    height: 100vh;
}

#header-div {
    background-color: var(--priority-low-color);
    height: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#header-text {
    color: var(--secondary-color);
}

#menu-div {
    background-color: var(--priority-low-color);
    opacity: .9;
    height: 10%;
    display: flex;
}

#project-menu-div {
    height: 100%;
    display: flex;
    justify-content: center;
    width: 50%;
}

#project-menu-list-form {
    width: 25%;
